<template>
  <el-table
    :data="planData"
    style="width: 100%;"
    :default-sort = "{prop: 'time'}">
    <el-table-column
      label="Êó∂Èó¥"
      prop="time"
      header-align="center"
      algin="center">
      <template slot-scope="scope">
        <i class="el-icon-time"></i>
        <span style="margin-left: 10px">{{ scope.row.time }}</span>
      </template>
    </el-table-column>
    <el-table-column
      label="ËÆ°Âàí"
      prop="plan"
      header-align="center"
      algin="center">
      <template slot-scope="scope">
        <span>{{ scope.row.plan }}</span>
      </template>
    </el-table-column>
    <el-table-column
      header-align="center"
      algin="center">
      <template slot-scope="scope">
        <el-button
          size="mini"
          type="danger"
          @click="delData(scope.row.id)">Âà†Èô§</el-button>
      </template>
    </el-table-column>
  </el-table>
</template>

<script>
import {mapState, mapActions} from 'vuex'

export default {
  methods: {
    ...mapActions({
      // Ëé∑ÂèñÊï∞ÊçÆ
      getData: 'getPlanData',
      // Âà†Èô§Êï∞ÊçÆ
      delData: 'delPlanData'
    })
  },
  computed: {
    ...mapState({
      // ËÆ°ÂàíÊï∞ÊçÆ
      planData: state => state.plan.planData,
      // ÂºπÁ™ó‰ø°ÊÅØ
      alertMsg: state => state.plan.alertMsg,
      // ÂºπÁ™óÁ±ªÂûã
      alertType: state => state.plan.alertType
    })
  },
  watch: {
    // Ê∂àÊÅØÊèêÁ§∫
    alertMsg (val) {
      if (val !== '') {
        this.$message({
          message: this.alertMsg,
          type: this.alertType
        })
      }
    }
  },
  created () {
    this.getData()
  }
}
</script>

<style scoped>
  .el-table{
    box-shadow: 0 2px 4px rgba(0, 0, 0, .12), 0 0 6px rgba(0, 0, 0, .04);
  }
</style>
<style>
  .el-table td, .el-table th{
    text-align: center;
  }
</style>



