import Axios from 'axios'

export default{
  state: {
    planData: [],
    alertMsg: '',
    alertType: ''
  },
  mutations: {
    // Ëé∑ÂèñËÆ°ÂàíÊï∞ÊçÆ
    getPlanData (state, res) {
      state.planData = res.data
      // console.log(state.planData)
    },
    // Âà†Èô§ËÆ°ÂàíÊï∞ÊçÆ
    delSuccess (state, payload) {
      state.alertMsg = 'Âà†Èô§ËÆ°ÂàíÊàêÂäüÔºÅ'
      state.alertType = 'success'
      // Êü•ÊâæË¢´Âà†ÊéâÁöÑÊï∞ÊçÆÂπ∂Âà†Èô§
      const i = state.planData.findIndex(val => val.id === payload)
      state.planData.splice(i, 1)
    },
    delError (state, res) {
      state.alertMsg = 'Âà†Èô§ËÆ°ÂàíÂ§±Ë¥•ÔºÅ ' + res.status
      state.alertType = 'error'
    },
    // Ê∑ªÂä†ËÆ°ÂàíÊï∞ÊçÆ
    addSuccess (state, payload) {
      state.alertMsg = 'Ê∑ªÂä†ËÆ°ÂàíÊàêÂäüÔºÅ'
      state.alertType = 'success'
      // Êõ¥Êñ∞Êñ∞Â¢ûÊï∞ÊçÆ
      state.planData.push(payload)
    },
    addError (state, res) {
      state.alertMsg = 'Ê∑ªÂä†ËÆ°ÂàíÂ§±Ë¥•ÔºÅ ' + res.status
      state.alertType = 'error'
    },
    // Ê∏ÖÈô§Ê∂àÊÅØÁ™óÂè£‰ø°ÊÅØ
    clearMsg (state) {
      state.alertMsg = ''
      state.alertType = ''
    }
  },
  actions: {
    // Ëé∑ÂèñËÆ°ÂàíÊï∞ÊçÆ
    getPlanData (context) {
      Axios.get('/planData')
        .then(res => {
          context.commit('getPlanData', res)
        })
        .catch(err => console.log(err))
    },
    // Âà†Èô§ËÆ°ÂàíÊï∞ÊçÆ
    delPlanData (context, payload) {
      Axios.delete('/planData/' + payload)
        .then(res => {
          // console.log(res)
          if (res.status === 200) {
            context.commit('delSuccess', payload)
          } else {
            context.commit('delError', res)
          }
        })
        .then(context.commit('clearMsg'))
        .catch(err => console.log(err))
    },
    // Ê∑ªÂä†ËÆ°ÂàíÊï∞ÊçÆ
    addPlanData (context, payload) {
      // console.log(payload)
      Axios.post('/planData', payload)
        .then(res => {
          // console.log(res)
          if (res.status === 201) {
            context.commit('addSuccess', res.data)
          } else {
            context.commit('addError', res)
          }
        }).then(context.commit('clearMsg'))
        .catch(err => console.log(err))
    }
  }
}



