<template>
  <el-table
    :data="examData.slice((currentPage - 1) * pagesize, currentPage * pagesize)"
    style="width: 100%;"
    :default-sort = "{prop: 'date'}"
    border>
    <el-table-column
      label="ËÄÉËØïÂêçÁß∞"
      prop="name"
      header-align="center"
      algin="center"
      width="300">
      <template slot-scope="scope">
        <span>{{ scope.row.name }}</span>
      </template>
    </el-table-column>
    <el-table-column
      label="Êó•Êúü"
      prop="date"
      header-align="center"
      algin="center"
      width="140">
      <template slot-scope="scope">
        <span>{{ scope.row.date }}</span>
      </template>
    </el-table-column>
    <el-table-column
      label="Êï∞Â≠¶"
      prop="grade.math"
      header-align="center"
      algin="center"
      width="80">
      <template slot-scope="scope">
        <span>{{ scope.row.grade.math }}</span>
      </template>
    </el-table-column>
    <el-table-column
      label="ËØ≠Êñá"
      prop="grade.Chinese"
      header-align="center"
      algin="center"
      width="80">
      <template slot-scope="scope">
        <span>{{ scope.row.grade.Chinese }}</span>
      </template>
    </el-table-column>
    <el-table-column
      label="Ëã±ËØ≠"
      prop="grade.English"
      header-align="center"
      algin="center"
      width="80">
      <template slot-scope="scope">
        <span>{{ scope.row.grade.English }}</span>
      </template>
    </el-table-column>
    <el-table-column
      label="ÁªºÂêà"
      prop="grade.synthesis"
      header-align="center"
      algin="center"
      width="80">
      <template slot-scope="scope">
        <span>{{ scope.row.grade.synthesis }}</span>
      </template>
    </el-table-column>
    <el-table-column
      label="ÊÄªÂàÜ"
      prop="sum"
      header-align="center"
      algin="center"
      width="80">
      <template slot-scope="scope">
        <span>{{ scope.row.sum }}</span>
      </template>
    </el-table-column>
    <el-table-column
      label="Â§ÑÁêÜ"
      header-align="center"
      algin="center">
      <template slot-scope="scope">
        <el-button
          size="mini"
          @click="dialogShow();getEidtExam(scope.row)"
        >ÁºñËæë</el-button>
        <el-popconfirm
          title="Á°ÆÂÆöË¶ÅÂà†Èô§ËØ•Ë°åÊï∞ÊçÆÂêóÔºü"
          @onConfirm="delExam(scope.row.id)">
        <el-button
          size="mini"
          type="danger"
          slot="reference"
        >Âà†Èô§</el-button>
        </el-popconfirm>
      </template>
    </el-table-column>
  </el-table>
</template>

<script>
import {mapState, mapActions, mapMutations} from 'vuex'

export default {
  data () {
    return {
    }
  },
  methods: {
    ...mapActions({
      // Ëé∑ÂèñÊï∞ÊçÆ
      getExam: 'getExam',
      // Âà†Èô§Êï∞ÊçÆ
      delExam: 'delExam'
    }),
    ...mapMutations([
      // Êõ¥ÊîπÂØπËØùÊ°ÜÊòæÁ§∫Áä∂ÊÄÅ
      'dialogShow',
      // Ëé∑ÂèñÁºñËæëÁöÑËÄÉËØïÂÜÖÂÆπ
      'getEidtExam'
    ])
  },
  computed: {
    ...mapState({
      // ËÆ°ÂàíÊï∞ÊçÆ
      examData: state => state.exam.exam,
      // ÂºπÁ™ó‰ø°ÊÅØ
      alertMsg: state => state.exam.alertMsg,
      // ÂºπÁ™óÁ±ªÂûã
      alertType: state => state.exam.alertType,
      // ÊòØÂê¶ÊòæÁ§∫ÂØπËØùÊ°Ü
      dialogFormVisible: state => state.exam.dialogFormVisible,
      // ÈªòËÆ§ÂºÄÂßãÈ°µÈù¢
      currentPage: state => state.exam.currentPage,
      // ÊØèÈ°µÁöÑÊï∞ÊçÆÊù°Êï∞
      pagesize: state => state.exam.pagesize
    })
  },
  watch: {
    // Ê∂àÊÅØÊèêÁ§∫
    alertMsg (val) {
      if (val !== '') {
        this.$message({
          message: this.alertMsg,
          type: this.alertType
        })
      }
    }
  },
  created () {
    this.getExam()
  }
}
</script>

<style>
  .el-table td, .el-table th{
    text-align: center;
  }
</style>



