<template>
  <el-form :inline="true" :model="selForm" class="demo-form-inline">
    <el-form-item label="ËÄÉËØïÂêçÁß∞Êü•ËØ¢Ôºö">
      <el-input v-model="selForm.name" placeholder="ËÄÉËØïÂêçÁß∞" size="small" clearable></el-input>
    </el-form-item>
    <el-form-item label="Êó•ÊúüÔºö">
      <el-date-picker
        v-model="selForm.date"
        type="daterange"
        align="right"
        unlink-panels
        range-separator="Ëá≥"
        start-placeholder="ÂºÄÂßãÊó•Êúü"
        end-placeholder="ÁªìÊùüÊó•Êúü"
        size="small"
        style="width:220px;"
        value-format="yyyy-MM-dd"
        :picker-options="pickerOptions">
      </el-date-picker>
    </el-form-item>
    <el-form-item>
      <el-button type="primary" @click="onSubmit" size="small">Êü•ËØ¢</el-button>
      <el-button @click="getExam" size="small">ÊÅ¢Â§ç</el-button>
    </el-form-item>
  </el-form>
</template>

<script>
import {mapActions} from 'vuex'

export default {
  data () {
    return {
      selForm: {
        name: '',
        date: ''
      },
      selKey: '',
      pickerOptions: {
        shortcuts: [{
          text: 'ÊúÄËøë‰∏ÄÂë®',
          onClick (picker) {
            const end = new Date()
            const start = new Date()
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 7)
            picker.$emit('pick', [start, end])
          }
        }, {
          text: 'ÊúÄËøë‰∏Ä‰∏™Êúà',
          onClick (picker) {
            const end = new Date()
            const start = new Date()
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 30)
            picker.$emit('pick', [start, end])
          }
        }, {
          text: 'ÊúÄËøë‰∏â‰∏™Êúà',
          onClick (picker) {
            const end = new Date()
            const start = new Date()
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 90)
            picker.$emit('pick', [start, end])
          }
        }]
      }
    }
  },
  methods: {
    // Êõ¥Êñ∞ÊêúÁ¥¢ÂÖ≥ÈîÆÂ≠ó
    updataKey (form) {
      let arr = []
      let key = ''
      if (form.name) {
        arr.push(`name_like=${form.name}`)
      }
      if (form.date) {
        arr.push(`date_gte=${form.date[0]}&date_lte=${form.date[1]}`)
      }
      key = arr.join('&')
      return key
    },
    onSubmit () {
      this.selKey = this.updataKey(this.selForm)
      // console.log(this.selKey)
      this.selExam(this.selKey)
    },
    ...mapActions({
      // Êü•ËØ¢
      selExam: 'selExam',
      // Ëé∑ÂèñÊï∞ÊçÆ
      getExam: 'getExam'
    })
  }
}
</script>

<style>

</style>



