<template>
  <div class="formbox">
    <el-form ref="writeForm" :model="writeForm" label-width="100px">
      <el-form-item
        label="ËÄÉËØïÂêçÁß∞Ôºö"
        :rules="[{ required: true, message: '‰∏çËÉΩ‰∏∫Á©∫'}]"
        prop="name">
        <el-input v-model="writeForm.name"></el-input>
      </el-form-item>
      <el-form-item
        label="ËÄÉËØïÊó∂Èó¥Ôºö"
        :rules="[{ required: true, message: '‰∏çËÉΩ‰∏∫Á©∫'}]"
        prop="date">
        <el-date-picker
          v-model="writeForm.date"
          type="date"
          placeholder="ÈÄâÊã©Êó•Êúü"
          format="yyyy Âπ¥ MM Êúà dd Êó•"
          value-format="yyyy-MM-dd">
        </el-date-picker>
      </el-form-item>
      <el-form-item
        label="ËÄÉËØïÂàÜÊï∞Ôºö"
        :rules="[
          { required: true, message: '‰∏çËÉΩ‰∏∫Á©∫'},
          { type: 'number', message: 'ÂøÖÈ°ª‰∏∫Êï∞Â≠óÂÄº'}
        ]"
        prop="grade.math">
        <el-input class="grade" v-model.number="writeForm.grade.math" placeholder="Êï∞Â≠¶"></el-input>
      </el-form-item>
      <el-form-item
        :rules="[
          { required: true, message: '‰∏çËÉΩ‰∏∫Á©∫'},
          { type: 'number', message: 'ÂøÖÈ°ª‰∏∫Êï∞Â≠óÂÄº'}
        ]"
        prop="grade.Chinese">
      <el-input class="grade" v-model.number="writeForm.grade.Chinese" placeholder="ËØ≠Êñá"></el-input>
      </el-form-item>
      <el-form-item
        :rules="[
          { required: true, message: '‰∏çËÉΩ‰∏∫Á©∫'},
          { type: 'number', message: 'ÂøÖÈ°ª‰∏∫Êï∞Â≠óÂÄº'}
        ]"
        prop="grade.English">
      <el-input class="grade" v-model.number="writeForm.grade.English" placeholder="Ëã±ËØ≠"></el-input>
      </el-form-item>
      <el-form-item
        :rules="[
          { required: true, message: '‰∏çËÉΩ‰∏∫Á©∫'},
          { type: 'number', message: 'ÂøÖÈ°ª‰∏∫Êï∞Â≠óÂÄº'}
        ]"
        prop="grade.synthesis">
      <el-input class="grade" v-model.number="writeForm.grade.synthesis" placeholder="ÁªºÂêà"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="submitForm('writeForm')">Ê∑ªÂä†</el-button>
        <el-button @click="resetForm('writeForm')">ÈáçÁΩÆ</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import {mapState, mapActions} from 'vuex'

export default {
  data () {
    return {
      writeForm: {
        name: '',
        date: '',
        grade: {
          math: '',
          Chinese: '',
          English: '',
          synthesis: ''
        },
        sum: 0
      }
    }
  },
  methods: {
    ...mapActions({
      // Ê∑ªÂä†Êï∞ÊçÆ
      addExam: 'addExam'
    }),
    // Êèê‰∫§Ë°®Âçï
    submitForm (formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.writeForm.sum = this.gradeSum(this.writeForm.grade)
          // console.log(this.writeForm)
          this.addExam(this.writeForm)
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },
    // ÈáçÁΩÆË°®Âçï
    resetForm (formName) {
      this.$refs[formName].resetFields()
    },
    // ÂàÜÊï∞Ê±ÇÂíå
    gradeSum (grade) {
      let sum = 0
      for (const key in grade) {
        sum += grade[key]
      }
      return sum
    }
  },
  computed: {
    ...mapState({
      // ÂºπÁ™ó‰ø°ÊÅØ
      alertMsg: state => state.exam.alertMsg,
      // ÂºπÁ™óÁ±ªÂûã
      alertType: state => state.exam.alertType
    })
  },
  watch: {
    // Ê∂àÊÅØÊèêÁ§∫
    alertMsg (val) {
      if (val !== '') {
        this.$message({
          message: this.alertMsg,
          type: this.alertType
        })
      }
    }
  }
}
</script>

<style scoped>
  .formbox{
    margin: 10px 50px;
    padding: 50px 100px;
    background-color: #fff;
    border-radius: 5px;
    box-shadow: 0 2px 4px rgba(0, 0, 0, .12), 0 0 6px rgba(0, 0, 0, .04);
  }
  .grade{
    width: 130px;
    margin-right: 20px;
  }
</style>



