<template>
  <div id="myChart" :style="{width: '1100px', height: '425px', margin: 'auto'}">

  </div>
</template>

<script>
import {mapState, mapActions} from 'vuex'

export default {
  data () {
    return {
      newChartName: [],
      newChartNum: []
    }
  },
  methods: {
    ...mapActions({
      // Ëé∑ÂèñÊó•ÊúüÂâç12Êù°ËÄÉËØïÊï∞ÊçÆ
      getChartData: 'getChartData'
    }),
    // Ëé∑ÂèñËÄÉËØïÂêçÂ≠ó
    getChartName (data) {
      // ÂàùÂßãÂåñ
      this.newChartName = []
      data.map(val => this.newChartName.push(val.name))
      // console.log(this.newChartName)
    },
    // Êï∞ÊçÆÂ§ÑÁêÜ
    chartDataProcess (data, type) {
      // ÂàùÂßãÂåñ
      this.newChartNum = []
      if (type === 'sum') {
        data.map(val => this.newChartNum.push(val.sum))
      } else {
        data.map(val => this.newChartNum.push(val.grade[type]))
      }
      // console.log(this.newChartNum)
    },
    // yËΩ¥ÊúÄÂÄºÂ§ÑÁêÜ
    min (value) {
      return value.min - 10
    },
    max (value) {
      return value.max + 10
    },

    // ÁªòÂà∂ÂõæË°®
    drawLine () {
      let myChart = this.$echarts.init(document.getElementById('myChart'))
      // ÁªòÂà∂ÂõæË°®
      console.log(this.newChartName)
      myChart.setOption({
        title: { text: '‰ªÖÊòæÁ§∫ÊúÄËøëÊó•ÊúüÁöÑ12Êù°Êï∞ÊçÆÔºö' },
        tooltip: {},
        xAxis: {
          data: this.newChartName
        },
        yAxis: {
          min: this.min,
          max: this.max
        },
        series: [{
          name: 'ÊàêÁª©',
          type: 'line',
          symbolSize: 10,
          lineStyle: {
            width: 5
          },
          markPoint: {
            itemStyle: {
              color: 'chocolate'
            },
            data: [
              {name: 'ÊúÄÈ´òÂàÜ', type: 'max'},
              {name: 'ÊúÄ‰ΩéÂàÜ', type: 'min'}
            ]
          },
          data: this.newChartNum
        }]
      })
    }
  },
  computed: {
    ...mapState({
      // Êó•ÊúüÂâç12Êù°ËÄÉËØïÊï∞ÊçÆ
      chartData: state => state.chart.chartData,
      // ÊàêÁª©Á±ªÂûã
      gradeType: state => state.chart.type
    })
  },
  watch: {
    chartData (val) {
      this.getChartName(val)
    },
    gradeType (val) {
      this.chartDataProcess(this.chartData, val)
      this.drawLine()
    }
  },
  created () {
    this.getChartData()
  }
}
</script>

<style>
</style>



