<template>
  <el-dialog title="‰øÆÊîπËÄÉËØï‰ø°ÊÅØ" :visible.sync="dialogFormVisible" :before-close="dialogShow">
    <el-form :model="newEditExam" ref="newEditExam" label-width="100px">
      <el-form-item
          label="ËÄÉËØïÂêçÁß∞Ôºö"
          :rules="[{ required: true, message: '‰∏çËÉΩ‰∏∫Á©∫'}]"
          prop="name">
          <el-input v-model="newEditExam.name" clearable></el-input>
        </el-form-item>
        <el-form-item
          label="ËÄÉËØïÊó∂Èó¥Ôºö"
          :rules="[{ required: true, message: '‰∏çËÉΩ‰∏∫Á©∫'}]"
          prop="date">
          <el-date-picker
            v-model="newEditExam.date"
            type="date"
            placeholder="ÈÄâÊã©Êó•Êúü"
            format="yyyy Âπ¥ MM Êúà dd Êó•"
            value-format="yyyy-MM-dd">
          </el-date-picker>
        </el-form-item>
        <el-form-item
          label="Êï∞Â≠¶Ôºö"
          :rules="[
            { required: true, message: '‰∏çËÉΩ‰∏∫Á©∫'},
            { type: 'number', message: 'ÂøÖÈ°ª‰∏∫Êï∞Â≠óÂÄº'}
          ]"
          prop="grade.math">
          <el-input class="grade" v-model.number="newEditExam.grade.math" placeholder="Êï∞Â≠¶" clearable></el-input>
        </el-form-item>
        <el-form-item
          label="ËØ≠ÊñáÔºö"
          :rules="[
            { required: true, message: '‰∏çËÉΩ‰∏∫Á©∫'},
            { type: 'number', message: 'ÂøÖÈ°ª‰∏∫Êï∞Â≠óÂÄº'}
          ]"
          prop="grade.Chinese">
        <el-input class="grade" v-model.number="newEditExam.grade.Chinese" placeholder="ËØ≠Êñá" clearable></el-input>
        </el-form-item>
        <el-form-item
          label="Ëã±ËØ≠Ôºö"
          :rules="[
            { required: true, message: '‰∏çËÉΩ‰∏∫Á©∫'},
            { type: 'number', message: 'ÂøÖÈ°ª‰∏∫Êï∞Â≠óÂÄº'}
          ]"
          prop="grade.English">
        <el-input class="grade" v-model.number="newEditExam.grade.English" placeholder="Ëã±ËØ≠" clearable></el-input>
        </el-form-item>
        <el-form-item
          label="ÁªºÂêàÔºö"
          :rules="[
            { required: true, message: '‰∏çËÉΩ‰∏∫Á©∫'},
            { type: 'number', message: 'ÂøÖÈ°ª‰∏∫Êï∞Â≠óÂÄº'}
          ]"
          prop="grade.synthesis">
        <el-input class="grade" v-model.number="newEditExam.grade.synthesis" placeholder="ÁªºÂêà" clearable></el-input>
        </el-form-item>
    </el-form>
    <div slot="footer" class="dialog-footer">
      <el-button @click="dialogShow">Âèñ Ê∂à</el-button>
      <el-button type="primary" @click="submitForm('newEditExam')">Á°Æ ÂÆö</el-button>
    </div>
  </el-dialog>
</template>

<script>
import {mapState, mapMutations, mapActions} from 'vuex'

export default {
  data () {
    return {
      newEditExam: {
        name: '',
        date: '',
        grade: {
          math: '',
          Chinese: '',
          English: '',
          synthesis: ''
        },
        sum: 0
      }
    }
  },
  methods: {
    ...mapMutations([
      // ÊòØÂê¶ÊòæÁ§∫
      'dialogShow'
    ]),
    ...mapActions({
      // ‰øÆÊîπÊï∞ÊçÆ
      editExam: 'editExam'
    }),
    // Êèê‰∫§Ë°®Âçï
    submitForm (formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.newEditExam.sum = this.gradeSum(this.newEditExam.grade)
          // console.log(this.newEditExam)
          this.editExam(this.newEditExam)
          this.dialogShow()
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },
    // ÂàÜÊï∞Ê±ÇÂíå
    gradeSum (grade) {
      let sum = 0
      for (const key in grade) {
        sum += grade[key]
      }
      return sum
    }
  },
  computed: {
    ...mapState({
      // ÊòØÂê¶ÊòæÁ§∫ÂØπËØùÊ°Ü
      dialogFormVisible: state => state.exam.dialogFormVisible,
      // ÁºñËæëÁöÑËÄÉËØïÂÜÖÂÆπ
      editExamData: state => state.exam.editExamData
    })
  },
  watch: {
    editExamData (val) {
      this.newEditExam = val
    }
  }
}
</script>

<style scoped>
  .grade{
    width: 130px;
    margin-right: 20px;
  }
  .el-form-item{
    text-align: left;
  }
</style>



