import Axios from 'axios'

export default{
  state: {
    exam: [],
    alertMsg: '',
    alertType: '',
    dialogFormVisible: false,
    editExamData: {
      name: '',
      date: '',
      grade: {
        math: 0,
        Chinese: 0,
        English: 0,
        synthesis: 0
      },
      sum: 0
    },
    // ÂàÜÈ°µ
    total: 1, // ÈªòËÆ§Êï∞ÊçÆÊÄªÊï∞
    pagesize: 10, // ÊØèÈ°µÁöÑÊï∞ÊçÆÊù°Êï∞
    currentPage: 1 // ÈªòËÆ§ÂºÄÂßãÈ°µÈù¢
  },
  mutations: {
    // ÂºπÂá∫Ê°ÜÊ∂àÊÅØÂèäÂêéÁª≠Êï∞ÊçÆÂ§ÑÁêÜ
    msgSuccess (state, payload) {
      state.alertMsg = payload.msg
      state.alertType = 'success'
      // Êï∞ÊçÆÂ§ÑÁêÜ
      switch (payload.type) {
        case 'add':
          // Ê∑ªÂä†Êï∞ÊçÆ
          state.exam.push(payload.data)
          break
        case 'del':
          // Êü•ÊâæË¢´Âà†ÊéâÁöÑÊï∞ÊçÆÂπ∂Âà†Èô§
          const d = state.exam.findIndex(val => val.id === payload.id)
          state.exam.splice(d, 1)
          break
        case 'edit':
          // Êõ¥Êñ∞‰øÆÊîπÁöÑÊï∞ÊçÆ
          const e = state.exam.findIndex(val => val.id === payload.data.id)
          state.exam.splice(e, 1, payload.data)
          break
        default:
          break
      }
    },
    msgError (state, payload) {
      state.alertMsg = payload.msg + payload.data.status
      state.alertType = 'error'
    },
    // Ëé∑ÂèñËÆ°ÂàíÊï∞ÊçÆ
    getExam (state, res) {
      state.exam = res.data
      // console.log(state.exam)
      // Êõ¥Êñ∞Êï∞ÊçÆÊÄªÊï∞
      state.total = state.exam.length
      // ÂàùÂßãÂΩìÂâçÈ°µ‰∏∫1
      state.currentPage = 1
    },
    // ÂΩìÂâçÈ°µÊõ¥Êñ∞
    currentChange (state, currentPage) {
      state.currentPage = currentPage
    },
    // Ê∏ÖÈô§Ê∂àÊÅØÁ™óÂè£‰ø°ÊÅØ
    clearMsg (state) {
      state.alertMsg = ''
      state.alertType = ''
    },
    // ÊòæÁ§∫ÈöêËóèÂØπËØùÊ°Ü
    dialogShow (state) {
      state.dialogFormVisible = !state.dialogFormVisible
    },
    // Ëé∑ÂèñÁºñËæëÁöÑËÄÉËØïÂÜÖÂÆπ
    getEidtExam (state, payload) {
      state.editExamData = Object.assign({}, payload)
    }
  },
  actions: {
    // Ê∑ªÂä†ËÄÉËØïÊï∞ÊçÆ
    addExam (context, payload) {
      // console.log(payload)
      Axios.post('/exam', payload)
        .then(res => {
          // console.log(res)
          if (res.status === 201) {
            context.commit('msgSuccess', {
              data: res.data,
              type: 'add',
              msg: 'Ê∑ªÂä†ËÄÉËØïÊàêÁª©ÊàêÂäüÔºÅ'
            })
          } else {
            context.commit('msgError', {
              data: res.data,
              type: 'add',
              msg: 'Ê∑ªÂä†ËÄÉËØïÊàêÁª©Â§±Ë¥•ÔºÅ'
            })
          }
        })
        .then(context.commit('clearMsg'))
        .catch(err => console.log(err))
    },
    // Ëé∑ÂèñËÄÉËØïÊï∞ÊçÆ
    getExam (context) {
      Axios.get('/exam?_sort=date&_order=asc')
        .then(res => {
          context.commit('getExam', res)
        })
        .catch(err => console.log(err))
    },
    // Êü•ÊâæËÄÉËØïÊï∞ÊçÆ
    selExam (context, payload) {
      // console.log(payload)
      Axios.get('/exam?' + payload)
        .then(res => {
          // console.log(res)
          context.commit('getExam', res)
        })
        .catch(err => console.log(err))
    },
    // Âà†Èô§ËÄÉËØïÊï∞ÊçÆ
    delExam (context, payload) {
      Axios.delete('/exam/' + payload)
        .then(res => {
          // console.log(res)
          if (res.status === 200) {
            context.commit('msgSuccess', {
              id: payload,
              type: 'del',
              msg: 'Âà†Èô§ËÄÉËØïÊàêÁª©ÊàêÂäüÔºÅ'
            })
          } else {
            context.commit('msgError', {
              data: res.data,
              type: 'del',
              msg: 'Âà†Èô§ËÄÉËØïÊàêÁª©Â§±Ë¥•ÔºÅ'
            })
          }
        })
        .then(context.commit('clearMsg'))
        .catch(err => console.log(err))
    },
    // ‰øÆÊîπËÄÉËØïÊï∞ÊçÆ
    editExam (context, payload) {
      Axios.put('/exam/' + payload.id, payload)
        .then(res => {
          // console.log(res)
          if (res.status === 200) {
            context.commit('msgSuccess', {
              data: res.data,
              type: 'edit',
              msg: '‰øÆÊîπËÄÉËØïÊàêÁª©ÊàêÂäüÔºÅ'
            })
          } else {
            context.commit('msgError', {
              data: res.data,
              type: 'edit',
              msg: '‰øÆÊîπËÄÉËØïÊàêÁª©Â§±Ë¥•ÔºÅ'
            })
          }
        })
        .then(context.commit('clearMsg'))
        .catch(err => console.log(err))
    }
  }
}



